function Erying() {
    return <h2>二营</h2>
}

function Qibinglian(props) {
    console.log(props)

    return <h2>骑兵连</h2>
}

class Test extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        console.log(this.props)
        // setTimeout(() => {
        //     this.props.history.push("/")
        // }, 2000)

        if (this.props.history.location.search == '?qibinglian') {
            this.props.history.push("qibinglian")
            return null
        }

        return <h2>测试组件 {this.props.match.params.location}</h2>
    }
}


ReactDom.render(
    (<Provider store={store}>
        <BrowserRouter>
            <div>
                <ul>
                    <li><Link to="/">一营</Link></li>
                    <li><Link to="erying">二营</Link></li>

                    <li><Link to="/index.html?qibinglian">骑兵连1</Link></li>
                    <li><Link to="login">登录</Link></li>
                </ul>
                <Switch>
                    {/*只渲染命中的第一个Route*/}
                    <Route exact path="/" component={App}></Route>
                    <Route path='/login' component={Auth}></Route>
                    <Route path="/erying" component={Erying}></Route>
                    <Route path="/qibinglian" component={Qibinglian}></Route>
                    <Route path="/:location" component={Test}></Route>

                </Switch>


            </div>
        </BrowserRouter>
    </Provider>),
    document.getElementById("root")
)

